const log = (...params: any[]) => {
  console.log(...params);
  if (params.length === 0) {
    console.log("console.log");
    console.log("console.log(number,string,object)");
    console.log(",  %s %d,%i , %f ");
    console.log("%o =console.dirxml %O =console.dir");
    console.log("%c  example: ");
    console.log("console.log('%c','font-size: 30px;')");
    console.log(...allConsole);
    console.log("%c", "font-size:30px;color: blue;");
  }
};
const info = (...params: any[]) => {
  console.info(...params);
  if (params.length === 0) {
    console.info("log");
  }
};
const debug = (...params: any[]) => {
  console.debug(...params);
  if (params.length === 0) {
    console.log("log");
    console.debug("firefox");
  }
};
