const chromeHelper = () => {
  const intro = `
    $ //  document.querySelector 
    $$ //  document.querySelectorAll 
    $_ // 
    $0-$4 // 5ElementsDOM
    dir //  console.dir
    keys // , 
    values // , 
    monitor() 
    unmonitor
    inspect(object/function)
    profile
    escape 
  `
  console.log(intro);
  console.log(...consoleCmd);
};

